import Rete from "rete";
import VueSliderControl from "./SliderControl.vue";

export class SliderControl extends Rete.Control {
  component: any;
  props:any;
  vueContext:any;
  constructor(emitter:any, key:any, readonly:any) {
    super(key);
    this.component = VueSliderControl;
    this.props = { emitter, ikey: key, readonly };
  }

  setValue(val:any) {
    this.vueContext.value = val;
  }
}
