import Vue from "vue";
import Rete from "rete";
import VueRenderPlugin from "rete-vue-render-plugin";
import ConnectionPlugin from "rete-connection-plugin";
import AreaPlugin from "rete-area-plugin";
import ContextMenuPlugin from "rete-context-menu-plugin";
// import { TextControl } from "./controls/TextControl.js";
import { NoiseComponent } from "./components/NoiseComponent";
import { BlurComponent } from "./components/BlurComponent";
// import * as Socket from "./sockets";

export default async function(container : HTMLElement, propertyPanel : HTMLElement) {
  const components = [
    new NoiseComponent(),
    new BlurComponent()
  ];
  const editor = new Rete.NodeEditor("demo@0.1.0", container);
  editor.use(ConnectionPlugin);
  editor.use(VueRenderPlugin);
  editor.use(ContextMenuPlugin);
  editor.use(AreaPlugin);

  const engine = new Rete.Engine("demo@0.1.0");

  components.map(c => {
    editor.register(c);
    engine.register(c);
  });

  console.log(propertyPanel);
  /*editor.on(
    "process" || "nodecreated" || "noderemoved" || "connectioncreated" || "connectionremoved",
    async () => {
      await engine.abort();
      await engine.process(editor.toJSON());
    }
  );*/
  
  const properties:Vue[] = [];

  editor.on(
    "nodeselect",
    async (node) => {
      //propertyPanel.bindNode(node);
      properties.forEach((ele, id, ar)=>{
        ele.$destroy();
        ele.$el.parentNode?.removeChild(ele.$el);
      })
      node.controls.forEach(element => {
        const vueComponent = (element as any).component;
        const props = (element as any).props;
        const vueProps = { ...props, getData: element.getData.bind(element), putData: element.putData.bind(element) }
        const app = new Vue({
            render: h => h(vueComponent, { props: vueProps }),
        });
    
        const nodeEl = document.createElement('div');
        properties.push(app);
        propertyPanel.appendChild(nodeEl);
        app.$mount(nodeEl);
      });
    }
  );

  editor.on(
    "process",
    async () => {
      await engine.abort();
      await engine.process(editor.toJSON());
    }
  );

  editor.on(
    "nodecreated",
    async () => {
      await engine.abort();
      await engine.process(editor.toJSON());
    }
  );

  editor.on(
    "noderemoved",
    async () => {
      await engine.abort();
      await engine.process(editor.toJSON());
    }
  );

  editor.on(
    "connectioncreated",
    async () => {
      await engine.abort();
      await engine.process(editor.toJSON());
    }
  );

  editor.on(
    "connectionremoved",
    async () => {
      await engine.abort();
      await engine.process(editor.toJSON());
    }
  );

  //on press keyboard
  editor.on('keydown', e => {
    switch (e.code) {
    case 'Delete':{
        editor.selected.each(n => editor.removeNode(n));
        break;
      }
    case 'Space':{
        const rect = editor.view.container.getBoundingClientRect();
        const event = new MouseEvent('contextmenu', {
            clientX: rect.left + rect.width / 2,
            clientY: rect.top + rect.height / 2
        });
        editor.trigger('contextmenu', { e: event, view: editor.view }); 
        break;
      }
    default: break;
    }
  });

  editor.view.resize();
  AreaPlugin.zoomAt(editor);

  setTimeout(() => editor.trigger("process"), 1000);
}