export default 

class Taichi {
    moduleTemplate:any;
    module:any;
    constructor(module:any) {
        this.moduleTemplate = module;
    }

    set_arg_int(index:number, value:number) {
        const args_base = this.module._Ti_args/4 + index * 2;
        const args = this.module.HEAP32.subarray(args_base, args_base + 1);

        args[0] = value;
    }

    set_arg_float(index:number, value:number) {
        const args_base = this.module._Ti_args/4 + index * 2;
        const args = this.module.HEAPF32.subarray(args_base, args_base + 1);

        args[0] = value;
    }

    get_ret_int(index:number, value:number) {
        const args_base = this.module._Ti_args/4 + index * 2;
        const args = this.module.HEAP32.subarray(args_base, args_base + 1);

        return args[0];
    }

    get_ret_float(index:number, value:number) {
        const args_base = this.module._Ti_args/4 + index * 2;
        const args = this.module.HEAPF32.subarray(args_base, args_base + 1);

        return args[0];
    }

    set_ext_arr(index:number, shape:Array<number>) {
        const earg_base = this.module._Ti_earg/4 + index * 8;
        const args_base = this.module._Ti_args/4 + index * 2;
        const earg = this.module.HEAP32.subarray(earg_base, earg_base + 8);
        const args = this.module.HEAP32.subarray(args_base, args_base + 1);

        args[0] = this.module._Ti_extr;

        let size = 1;
        for (let i = 0; i < shape.length; i++) {
            earg[i] = shape[i];
            size *= shape[i];
        }

        return size;
    }

    set_ext_arr_float(index:number, shape:Array<number>):Float32Array {
        const size = this.set_ext_arr(index, shape);
        const extr_base = this.module._Ti_root/4;
        const extr = this.module.HEAPF32.subarray(extr_base, extr_base + size);
        return extr;
    }

    set_ext_arr_int(index:number, shape:Array<number>) {
        const size = this.set_ext_arr(index, shape);
        const extr_base = this.module._Ti_root/4;
        const extr = this.module.HEAP32.subarray(extr_base, extr_base + size);
        return extr;
    }

    set_ext_arr_uint8(index:number, shape:Array<number>) {
        const size = this.set_ext_arr(index, shape);
        const extr_base = this.module._Ti_extr;
        const extr = this.module.HEAPU8.subarray(extr_base, extr_base + size);
        return extr;
    }

    get(name:string) {
        let ret = this.module['_Tk_' + name];
        if (typeof ret == 'undefined') {
            for (const key in this.module) {
                if (key.startsWith('_Tk_' + name)) {
                    ret = this.module[key];
                    this.module['_Tk_' + name] = ret;
                    break;
                }
            }
        }
        if (typeof ret == 'undefined')
            return undefined;
        const mod = this.module;
        return function() {
            return ret(mod._Ti_ctx);
        }.bind(this);
    }

    get_config_int(name:string) {
        const base = this.module['_Ti_cfg_' + name];
        if (typeof base == 'undefined')
            return undefined;
        return this.module.HEAP32[base/4];
    }

    get_config_float(name:string) {
        const base = this.module['_Ti_cfg_' + name];
        if (typeof base == 'undefined')
            return undefined;
        return this.module.HEAPF32[base/4];
    }

    get_config_str(name:string) {
        const base = this.module['_Ti_cfg_' + name];
        if (typeof base == 'undefined')
            return undefined;
        let ret = '';
        for (let i = base; this.module.HEAPU8[i] != 0; i++) {
            ret += String.fromCharCode(this.module.HEAPU8[i]);
        }
        return ret;
    }
    
    async ready(cb:any) {
        this.module = await new this.moduleTemplate();
        cb();
    }
}