import Rete from "rete";
import CustomNode from "../visual/CustomNode.vue";
import * as Socket from "../sockets";
import {kernel} from "../../main"
import { SliderControl } from "../controls/SliderControl";

export class NoiseComponent extends Rete.Component {
  data: any;
  constructor() {
    super("Noise");
    this.data.render = 'vue';
    this.data.component = CustomNode;
  }

  builder(node: any) {
    const out = new Rete.Output("result", "Output", Socket.value);
    const control = new SliderControl(this.editor, "Scale", false);

    node.data['node']=node;
    node.data['cache']=new Float32Array(65536);
    node.data['Scale']=10.0;

    return node
      .addControl(control)
      .addOutput(out);
  }

  worker(node: any, inputs: any, outputs: any) {
    const run = kernel.module.get('perlin_c4_0');
    kernel.module.set_arg_float(0, 256)
    kernel.module.set_arg_float(1, node.data['Scale'])
    const arr = kernel.module.set_ext_arr_float(2, [256,256])
    run && run();
    node.data['node'].vueContext.setImage(arr);
    node.data['cache'].set(arr);
    outputs["result"] = node.data['cache'];
  }
}
