import Rete from "rete";
import CustomNode from "../visual/CustomNode.vue";
import * as Socket from "../sockets";
import {kernel} from "../../main"
import { SliderControl } from "../controls/SliderControl";

export class BlurComponent extends Rete.Component {
  data:any;
  constructor() {
    super("Blur"),
    this.data.component = CustomNode;
  }

  builder(node: any) {
    const nodeIn = new Rete.Input("input", "Input", Socket.value);
    const out = new Rete.Output("result", "Output", Socket.value);
    const control = new SliderControl(this.editor, "Intensity", false);
    node.data['Intensity']=4.0;

    node.data['node']=node;
    node.data['cache']=new Float32Array(65536);

    return node
      .addControl(control)
      .addInput(nodeIn)
      .addOutput(out);
  }

  worker(node: any, inputs: any, outputs: any) {
    if(inputs["input"].length>0){
      const input = inputs["input"][0];
      const run = kernel.module.get('boxblur_c8_0');
      kernel.module.set_arg_int(0, node.data['Intensity']);
      const arrin = kernel.module.set_ext_arr_float(2, [256,256]);
      arrin.set(input);
      run && run();
      node.data['node'].vueContext.setImage(arrin);
      node.data['cache'].set(arrin);
      outputs["result"] = node.data['cache'];
    }else{
      node.data['node'].vueContext.clear();
    }
  }
}
