<template>
<div>
  <div class="property-title">{{ikey}}</div>
  <input type="range" :step="step" :min="min" :max="max" class="slider" :readonly="readonly" :value="value" @input="change($event)"/>
  <div class="property-text">{{value}}</div>
</div>
</template>


<script>
export default {
  props: ["readonly", "emitter", "ikey", "getData", "putData"],
  data() {
    return {
      value: 0,
      min:1,
      max:20,
      step:1,
    };
  },
  methods: {
    change(e) {
      this.value = e.target.value;
      this.update();
    },
    update() {
      if (this.ikey) this.putData(this.ikey, this.value);
      this.emitter.trigger("process");
    }
  },
  mounted() {
    this.value = this.getData(this.ikey);
  }
}
</script>

<style>
select, input {
  width: 100%;
  border-radius: 0px;
  background-color: white;
  padding: 2px 6px;
  border: 1px solid #999;
  font-size: 110%;
  width: 170px;
  margin: 5px;
}
.property-title{
  color: white;
  display: inline-block;
  font-family: Monospace;
  font-size: 14px;
  display:block;
}
.property-text{
  color: white;
  display: inline-block;
  font-family: Monospace;
  font-size: 14px;
}
.slider{
  border:0;
  padding:0;
  margin:0;
  height:10px;
}
</style>

