<template>
<div class="node" :class="[selected(), node.name] | kebab">
  <div class="title">{{node.name}}</div>
  <div class="content">
    <div class="col" v-if="node.controls.size&gt;0 || node.inputs.size&gt;0">
      <div class="input" v-for="input in inputs()" :key="input.key" style="text-align: left">
        <Socket v-socket:input="input" type="input" :socket="input.socket" :used="() => input.connections.length"></Socket>
        <div class="input-title" v-show="!input.showControl()">{{input.name}}</div>
        <div class="input-control" v-show="input.showControl()" v-control="input.control"></div>
     </div>
    </div>
    <div class="col">
      <div class="output" v-for="output in outputs()" :key="output.key">
        <div class="output-title">{{output.name}}</div>
        <Socket v-socket:output="output" type="output" :socket="output.socket" :used="() => output.connections.length"></Socket>
      </div>
    </div>
  </div> 
  <canvas id="canvas" ref="myCanvas" width="256" height="256">no HTML5 support.</canvas>
</div>
</template>

<script>
import Render from 'rete-vue-render-plugin'

export default {
  mixins: [Render.mixin],
  components: {
    Socket: Render.Socket
  },
  methods: {
      setImage:function(image) {
        let canvas = this.$refs.myCanvas;
        let context = canvas.getContext('2d');
        let imgData = context.createImageData(canvas.width, canvas.height);
        let data = imgData.data;
        //Note that for hacking, input is grey!
        for (let y = 0; y < canvas.height; y++) {
            for (let x = 0; x < canvas.width; x++) {
                let i = (y * canvas.width + x) * 4;
                let j = (x * canvas.height + (canvas.height - 1 - y));
                data[i++] = parseInt(image[j] * 255);
                data[i++] = parseInt(image[j] * 255);
                data[i++] = parseInt(image[j] * 255);
                data[i++] = 255;
            }
        }
        context.putImageData(imgData, 0, 0);
      },
      clear:function() {
        let canvas = this.$refs.myCanvas;
        let context = canvas.getContext('2d');
        let imgData = context.createImageData(canvas.width, canvas.height);
        let data = imgData.data;
        //Note that for hacking, input is grey!
        for (let y = 0; y < canvas.height; y++) {
            for (let x = 0; x < canvas.width; x++) {
                let i = (y * canvas.width + x) * 4;
                let j = (x * canvas.height + (canvas.height - 1 - y));
                data[i++] = 0;
                data[i++] = 0;
                data[i++] = 0;
                data[i++] = 255;
            }
        }
        context.putImageData(imgData, 0, 0);
      }
  }
}
</script>

<style>
.thumbnail {
  width: 200px;
  height:200px;
}
</style>
