import Vue from "vue";
import App from "./App.vue"
import "./assets/style.css";
import VueRx from "vue-rx";
// @ts-ignore
import VTooltip from "v-tooltip";
import Taichi from "./kernel/taichi"
import Module from "../public/js/kernel.js"

Vue.config.productionTip = false;

class Kernel {
  module : Taichi;
  constructor(module: any) {
    this.module = new Taichi(module);
    this.module.ready(function() {console.log("Kernel Loaded");});
  }
}

export const kernel = new Kernel(Module);

Vue.use(VueRx);
Vue.use(VTooltip);
/* eslint-disable no-new */
new Vue({
  render: h => h(App)
}).$mount("#app");
